.example {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    line-height: 1.5;
    background-color: #fff;
    color: #2f2f37;
    max-width: 960px;
    padding: 16px;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial,
      sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
    height: 100vh;
    // background: linear-gradient(rgba(73, 70, 70, 0), rgba(107, 104, 104, 0.9));
  }
  
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    margin-top: 0;
    margin-bottom: 0;
  }
  
  ul,
  ol {
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
  }
  
  .list {
    list-style: none;
  }
  
  .link {
    text-decoration: none;
  }
  
  .nav__link {
    text-decoration: none;
    padding: 10px 16px;
    margin: 4px;
    border-radius: 8px;
    background: #4fb53f;
    box-shadow: 0 10px 10px 0 #cccccc;
    display: inline-block;
  }
  
  .container {
    max-width: 1280px;
    padding-left: 30px;
    padding-right: 30px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .search-form {
    width: 600px;
    display: flex;
    margin: 0 auto;
    margin-bottom: 40px;
  }
  
  .search-input {
    font-size: 18px;
    flex-grow: 1;
    height: 40px;
    padding-left: 15px;
    border: 1px solid #303030;
    border-radius: 8px;
  }
  
  .search-btn {
    font-size: 14px;
    padding: 10px 20px;
    background: #4fb53f;
    color: #ffffff;
    border: none;
    text-transform: uppercase;
    cursor: pointer;
    margin-left: 10px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    & span {
      margin-left: 8px;
    }
  }
  
  .weather__card {
    width: 300px;
    padding: 20px;
    box-shadow: 0 0 10px 0 #cccccc;
    background: #dcdcbfa3;
    font-weight: 500;
    color: #1d2323;
    border-radius: 8px;
  }
  
  .weather-info-item:not(:last-child) {
    margin-bottom: 10px;
  }
  
  .city-name {
    font-size: 26px;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 10px;
  }
  
  .gallery-section {
    padding: 60px 0;
  }
  
  .search-form {
    display: flex;
    width: 600px;
    margin: 0 auto;
    margin-bottom: 40px;
  }
  
  .search-input {
    border: none;
    outline: none;
    padding: 0.3em 1em;
    border-radius: 24px;
    background: #a4a4a42b;
    color: #fff;
    font-family: inherit;
    font-size: 1.25rem;
    width: calc(100% - 100px);
  }
  
  .load-more {
      display: block;
    font-size: 14px;
    padding: 10px 20px;
    background: #7c7c7c2b;
    color: #000;
    border: none;
    text-transform: uppercase;
    cursor: pointer;
    margin-left: 10px;
    margin: 0 auto;
    transition: background 0.2s ease-in-out;
  
    &:hover {
      background: #e7f0ffdf;
    }
  
  
  }
  
  .load-more.is-hidden {
    display: none;
  }
  
  .gallery, .meals {
      list-style:none;
    display: flex;
    justify-content:center;
    flex-wrap: wrap;
    margin-left: -30px;
    margin-top: -30px;
    margin-bottom: 20px;
  }
  
  .gallery__item, .meal__card {
    flex-basis: calc((100% - 120px) / 4);
    margin-left: 30px;
    margin-top: 30px;
  
  }
  
  .gallery-img {
    width: 100%;
    height: 100%;
    max-width: unset;
    object-fit: cover;
  }
  .posts-section {
    padding: 100px 0;
  }
  
  .posts {
    display: flex;
    flex-wrap: wrap;
    gap: 30px 5%;
    margin-bottom: 40px;
  }
  
  .posts__item {
    flex-basis: 30%;
    height: 340px;
    padding: 20px;
    box-shadow: 0 0 10px 0 #cccccc;
    overflow-y: auto;
  }
  
  .posts__title {
    font-size: 22px;
    margin-bottom: 20px;
  }
  
  .posts__text {
    font-size: 18px;
    margin-bottom: 10px;
    line-height: 1.4;
  }
  
  .load__more__btn {
    display: block;
    margin: 0 auto;
    min-width: 200px;
    padding: 14px 4px;
    background-color: #34495e;
    color: #ffffff;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 250ms linear;
  }
  
  .load__more__btn.is-hidden {
    display: none;
  }
  
  .load-more-btn:hover,
  .load-more-btn:focus {
    background-color: #2c3e50;
  }
  
  #search_btn {
    margin: 0.5em;
    border-radius: 50%;
    border: none;
    height: 44px;
    width: 44px;
    outline: none;
    background: #7c7c7c2b;
    color: #000;
    cursor: pointer;
    transition: background 0.2s ease-in-out;
  
    &:hover {
      background: #e7f0ffdf;
    }
  }
  
  .meal__card {
      border: 1px solid #FD5103;
      border-radius: 4px;
    }
    
    .meal__desc {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 900;
      font-size: 26px;
      line-height: 1.62;
      color: #202025;
      text-align: center;
      margin-top: 48px;
      margin-bottom: 16px;
    }
    